import React from "react";
import axios from "axios";

function App() {
  const [post, setPost] = React.useState({
    users: [],
    isLoading: true,
    errors: null
  });

  React.useEffect(() => {
    axios
      .get("https://randomuser.me/api/?results=500")
      .then((response) =>
        response.data.results.map((user) => ({
          name: `${user.name.first} ${user.name.last}`,
          username: `${user.login.username}`,
          email: `${user.email}`,
          image: `${user.picture.thumbnail}`
        }))
      )
      .then((users) => {
        setPost({
          users,
          isLoading: false
        });
      })
      .catch((error) => setPost({ error, isLoading: false }));
  }, []);

  if (!post) return null;
  return (
    <React.Fragment>
      <h2>Random User</h2>
      <div>
        {!post.isLoading ? (
          post.users.map((user) => {
            const { username, name, email, image } = user;
            return (
              <div key={username}>
                <p>{name}</p>
                <div>
                  <img src={image} alt={name} />
                </div>
                <p>{email}</p>
              </div>
            );
          })
        ) : (
          <p>Loading...</p>
        )}
      </div>
    </React.Fragment>
  );
}
export default App;
